<template>
  <mdb-navbar dark bg="primary" expand="lg" class="px-3 py-0 py-lg-3">
    <mdb-navbar-brand class="text-light px-0 py-2 py-lg-0 m-0">
      <b>Vue.js 3</b>
      &nbsp;currency converter
    </mdb-navbar-brand>

    <mdb-navbar-toggler @click="collapseContent = !collapseContent" />

    <mdb-collapse v-model="collapseContent">
      <mdb-navbar-nav class="d-flex justify-content-end flex-nowrap w-100">
        <mdb-navbar-item
          v-for="route in $router.getRoutes()"
          :key="route.name"
          :to="route.path"
          :active="route.name === $route.name"
        >
          {{ route.name }}
        </mdb-navbar-item>
      </mdb-navbar-nav>
    </mdb-collapse>
  </mdb-navbar>
</template>

<script>
import { ref } from "vue";

export default {
  name: "TheNavbar",

  setup() {
    return {
      collapseContent: ref(false)
    };
  },

  watch: {
    $route() {
      this.collapseContent = false;
    }
  }
};
</script>

<style scoped>
.navbar > * {
  position: relative;
}

.nav-item {
  font-size: 1.1rem;
  padding: 0 1rem;
}
</style>
