import { useToast } from "vue-toastification";
import axios from "axios";

const toast = useToast();

const NBP_API = "https://api.nbp.pl/api/exchangerates/tables/a/";

export default {
  fetchNBPApi({ commit }) {
    axios
      .get(NBP_API)
      .then(response => {
        const rates = response.data?.[0]?.rates;

        if (!rates?.length) throw "Invalid data fetched";

        let ratesObj = { PLN: 1 };

        for (const rate of rates) {
          ratesObj[rate.code] = rate.mid;
        }

        commit("setExchangeRates", ratesObj);
      })
      .catch(() => {
        toast.error("Fetching NBP Web API has failed!");
      });
  }
};
