<template>
  <mdb-container id="TheRouterViewContainer" fluid>
    <router-view v-slot="{ Component }" mode="out-in">
      <transition
        class="animate__animated animate__faster"
        enter-active-class="animate__fadeIn"
        leave-active-class="animate__fadeOut"
        mode="out-in"
        appear
      >
        <component :is="Component" />
      </transition>
    </router-view>

    <TheFooter />
  </mdb-container>
</template>

<script>
import TheFooter from "./TheFooter";

export default {
  name: "TheRouterViewContainer",

  components: { TheFooter },

  methods: {
    stretchContainer() {
      this.$el.style.height = window.innerHeight - this.$el.offsetTop + "px";
    }
  },

  mounted() {
    this.stretchContainer();
    window.addEventListener("resize", this.stretchContainer);
  },

  beforeUnmount() {
    window.removeEventListener("resize", this.stretchContainer);
  }
};
</script>

<style scoped>
#TheRouterViewContainer {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 1fr auto;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
  margin: 0;
  padding: 0;
  background-image: linear-gradient(black, black),
    url("../../assets/cash-annie-spratt-unsplash.jpg");
  background-blend-mode: saturation;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  background-attachment: fixed;
  z-index: 1;
}
</style>
